<template>
	<view>
		<slot name="container"></slot>
		<view class="drogue" :class="{ 'close': isClose }">
			<slot></slot>
		</view>
	</view>
</template>

<script>
	export default {
		name: "Drogue",
		props: {
			delay: {
				type: Number,
				default: 1500
			}
		},
		data() {
			return {
				isClose: false,
				timer: null
			}
		},
		destroyed() {
			this.destroyScroll()
		},
		methods: {
			destroyScroll() {
				clearTimeout(this.timer)
				this.timer = null
			},
			handleScroll() {
				this.isClose = true
				this.destroyScroll()
				this.timer = setTimeout(() => {
					this.isClose = false
				}, this.delay)
			}
		}
	}
</script>

<style>
	.drogue {
		position: fixed;
		right: 0;
		bottom: 50px;
		padding: 20px 10px;
		-webkit-transition: transform 0.3s;
		transition: transform 0.3s;
	}

	.close {
		-webkit-transform: translateX(650%);
		transform: translateX(65%);
	}
</style>
